import React, { useState } from 'react'
// import userData from '../datas/userData.json'
import { Link, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { BaseInputBox, BaseInput, BaseLabel, BaseButton } from '../styles/style';

const LoginForm = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Login = ({ userData, setUserInfo }) => {
    const [inputId, setInputId] = useState('');
    const [inputPw, setInputPw] = useState('');
    //   ?
    //   

    const navigate = useNavigate();

    const handleInputId = (event) => {
        setInputId(event.target.value);
    };

    const handleInputPw = (event) => {
        setInputPw(event.target.value);
    };

    //   onClick  
    // -> if        
    //  alert
    const loginBtn = (e) => {
        e.preventDefault();
        // inputId, inputPw ->     
        //   
        const arr = userData.filter(el => el.id === inputId && el.pw === inputPw);
        if (arr.length === 1) {
            navigate('/mypage');
            localStorage.setItem("userInfo",JSON.stringify(arr))
            setUserInfo(JSON.parse(localStorage.getItem('userInfo')))
            //     
            //   '*** '    
            //                
            //   js  js   App     
            // App useState nickname, setNickname ( null??)  setNickname props 
            //  nickName 
            //  -> ,   
        }
        else if (arr.length === 0) {
            alert('  ');
        }
    }

    return (
        <div className='wrap'>
            <LoginForm>
                <BaseInputBox>
                    <BaseInput type="text" id='ID' value={inputId} onChange={handleInputId} required/>
                    <BaseLabel htmlFor="ID"></BaseLabel>
                </BaseInputBox>
                {/* password  input*/}
                <BaseInputBox>
                    <BaseInput type="password" id='PASSWORD' value={inputPw} onChange={handleInputPw} required/>
                    <BaseLabel htmlFor="PASSWORD"></BaseLabel>
                </BaseInputBox>
                {/* ->  /  */}
                <BaseButton onClick={loginBtn}></BaseButton>
                {/*  ->*/}
                <Link to="/join"><BaseButton></BaseButton></Link>
                {/* ->    true       */}
                {/* close  -> Index.js 
        <button></button> */}
            </LoginForm>
        </div>
    )
}

export default Login